.main-header {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin: 0 auto;
  padding: 30px 103px;
  color: $color-default-white;

  @include vp-1199 {
    padding: 40px 45px 15px 45px;
  }

  @include vp-767 {
    padding: 15px;
  }
}

.main-header__phone {
  font-family: $font-family-primery;
  font-weight: $font-weight-semibold;
  font-size: 26px;
  line-height: 31px;
  letter-spacing: 0.78px;
  color: inherit;

  @include vp-1199 {
    font-weight: $font-weight-bold;
    font-size: 24px;
    line-height: 29px;
    letter-spacing: 0.48px;
  }

  @include vp-767 {
    font-weight: $font-weight-semibold;
    font-size: 20px;
    line-height: 24px;
    letter-spacing: 0.4px;
  }

  @include hover {
    opacity: 0.8;
  }
/*разобраться, как его связать с mixin*/
  &:active {
    color: $color-egyptian-blue;
  }

/*разобраться, как его связать с mixin*/
  &:focus-visible {
    outline: 2px solid $color-egyptian-blue;
  }
}
